import React from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";

// @material-ui/icons

// core components
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import CustomInput from "components/CustomInput/CustomInput.js";
import Button from "components/CustomButtons/Button.js";
import Email from "@material-ui/icons/Email";

import workStyle from "assets/jss/material-kit-pro-react/views/landingPageSections/workStyle.js";
import SnackbarContent from "components/Snackbar/SnackbarContent.js";
import { loadStripe } from '@stripe/stripe-js';
import data from '../../../data.json';

const useStyles = makeStyles(workStyle);
const stripePromise = loadStripe('pk_live_WKv81e8l4JbhiVi90rDrvVgk00718DlwgG');

export default function SectionWork() {
    const classes = useStyles();
    const [messageSent, setMessageSent] = React.useState(0);
    var feedbackFormValues = {};

    const handleChange = async (event) => {
          const { id, value } = event.target
          console.log(id)
          if (id=='email'){
            feedbackFormValues.email = value
          }
          if (id=='message'){
            feedbackFormValues.message = value
          }
          if (id=='name'){
            feedbackFormValues.name = value
          }
      }

    async function contactForm(e) {
      //setMessageSent(1)
      fetch('https://script.google.com/macros/s/AKfycbzCaGel-cANvlDicN-oCtaKLso1RSjZLsYXzMai3-yXxAnqTluD/exec?name='+encodeURI(feedbackFormValues.name)+'&email='+encodeURI(feedbackFormValues.email)+'&message='+encodeURI(feedbackFormValues.message + " / " + data['domain']))
          // When the customer clicks on the button, redirect
    // them to Checkout.
    const stripe = await stripePromise;
    const { error } = await stripe.redirectToCheckout({
      lineItems: [
        // Replace with the ID of your price
        {
          price: 'price_1GvDzQK6zduidqONmd6y9hil', 
          quantity: 1
        }
      ],
      mode: 'payment',
      successUrl: window.location + "?s=1&e="+encodeURI(feedbackFormValues.email),
      cancelUrl: window.location + "",
    });
  };

  return (
    <div className={classes.section}>
      <GridContainer justify="center">
        <GridItem cs={12} sm={12} md={8}>
          <h2 className={classes.title}>Try it now!</h2>
          <h4 className={classes.description}>
            Tell us here your order details.
          </h4>
            {messageSent > 0 ? <SnackbarContent key={feedbackFormValues.message} close message={"Thank you!  We will get back to you soon."}color="warning" icon={Email}/> : null}
          <form>
            <GridContainer>

              <GridItem xs={12} sm={12} md={6}>
                <CustomInput
                  labelText="Your Name"
                  id="name"
                  name="name"
                  formControlProps={{
                    fullWidth: true,
                    name: "name"
                  }}
                  inputProps={{
                    onChange: (event) => handleChange(event)
                  }}
                />
              </GridItem>
              <GridItem xs={12} sm={12} md={6}>
                <CustomInput
                  labelText="Your email where you will receive the order"
                  id="email"
                  name="email"
                  formControlProps={{
                    fullWidth: true,
                    name: "email"
                  }}
                  inputProps={{
                    onChange: (event) => handleChange(event)
                  }}
                />
              </GridItem>
              <CustomInput
                labelText="Write the information here."
                id="message"
                name="message"
                formControlProps={{
                  fullWidth: true,
                  className: classes.textArea,
                    name: "message"
                }}
                inputProps={{
                  multiline: true,
                  rows: 5,
                  onChange: (event) => handleChange(event)
                }}
              />
              <GridContainer justify="center">
                <GridItem xs={12} sm={12} md={4} className={classes.textCenter}>
                  <Button color="primary"
                  onClick={(e) => contactForm(e)}
                  >
                  Try it out
                  </Button>
                </GridItem>
              </GridContainer>
            </GridContainer>
          </form>
        </GridItem>
      </GridContainer>
    </div>
  );
}
